park\",\n  \"warning\": #ff9100 \"warning\",\n  \"danger\": #ff5252 \"spark\",\n  \"attention\": #ff5252 \"warning\",\n  \"error\": #ff5252 \"failure\",\n  \"hint\": #00c852 \"question\",\n  \"important\": #00bfa5 \"flame\",\n  \"note\": #00b0ff \"pencil\",\n  \"seealso\": #448aff \"info\",\n  \"tip\": #00c852 \"info\",\n  \"admonition-todo\": #808080 \"pencil\"\n);\n\n:root {\n  // Base Colors\n  --color-foreground-primary: black; // for main text and headings\n  --color-foreground-secondary: #5a5c63; // for secondary text\n  --color-foreground-border: #878787; // for content borders\n\n  --color-background-primary: white; // for content\n  --color-background-secondary: #f8f9fb; // for navigation + ToC\n  --color-background-border: #eeebee; // for UI borders\n\n  // Icons\n  @each $name, $glyph in $icons {\n    --icon-#{$name}: #{$glyph};\n  }\n  --icon-admonition-default: var(--icon-#{nth($admonition-default, 2)});\n\n  // Directives\n  --color-directive-unhandled-background: rgba(255, 255, 255, 0.3);\n  --color-directive-error-background: rgba(255, 0, 0, 0.4);\n\n  // Admonitions\n  --admonition-font-size: 1em;\n  --admonition-title-font-size: 1em;\n  --icon-admonition-default: var(--icon-#{nth($admonition-default, 2)});\n  // Note this background is transparent in furo, but we want dark text to still work for dark backgrounds\n  --color-admonition-background: rgba(255, 255, 255, 0.3);\n  --color-admonition-title: #{nth($admonition-default, 1)};\n  --color-admonition-title-background: #{rgba(nth($admonition-default, 1), 0.1)};\n\n  @each $name, $values in $admonitions {\n    --color-admonition-title--#{$name}: #{nth($values, 1)};\n    --color-admonition-title-background--#{$name}: #{rgba(nth($values, 1), 0.1)};\n  }\n}\n\n@media (prefers-color-scheme: dark) {\n  :root {\n    // Base Colors\n    --color-foreground-primary: #ffffffd9; // for main text and headings\n    --color-foreground-secondary: #9ca0a5; // for secondary text\n    --color-foreground-border: #666666; // for content borders\n\n    --color-background-primary: #131416; // for content\n    --color-background-secondary: #1a1c1e; // for navigation + ToC\n    --color-background-border: #303335; // for UI borders\n\n    // Directives\n    --color-directive-unhandled-background: #333338;\n    --color-directive-error-background: rgba(180, 6, 6, 0.8);\n\n    // Admonitions\n    --color-admonition-background: #18181a;\n  }\n}\n",".role-unhandled\n    outline: 1px solid lightgrey\n    border-radius: 0.2em\n    padding-left: 0.125em\n    padding-right: 0.125em\n\n    // the name of the role\n    mark\n        text-decoration-line: underline\n        background-color: unset\n        color: unset\n\n    // the content of the role\n    code\n        padding-left: 0.15em\n",".directive-unhandled, .directive-error\n    margin: 1em auto\n    padding: 0.5em\n    outline: 0.0625em solid lightgrey\n    border-radius: 0.2em\n    overflow: auto\n\n    // text color\n    color: var(--color-foreground-primary)\n\n    // First element should have no margin, since the title has it.\n    > :nth-child(2)\n        margin-top: 0\n\n    // Last item should have no margin, since we'll control that w/ padding\n    > :last-child\n        margin-bottom: 0\n\n    header\n        margin-bottom: 0.5em\n\n    // The name of the directive\n    mark\n        text-decoration-line: underline\n        background-color: unset\n        color: var(--color-foreground-primary)\n\n.directive-unhandled\n    outline: 0.0625em solid lightgrey\n    background-color: var(--color-directive-unhandled-background)\n\n.directive-error\n    outline: 0.0625em solid lightcoral\n    background-color: var(--color-directive-error-background)\n","// Copied from: https://github.com/pradyunsg/furo/blob/fe0088363a163cd9d6ffaf274560533501e935b5/src/furo/assets/styles/content/_admonitions.sass#L1\n.admonition\n  margin: 1em auto\n  padding: 0 0.5em 0.5em 0.5em\n\n  background: var(--color-admonition-background)\n  // copied from base/_theme.sass body\n  color: var(--color-foreground-primary)\n\n  border-radius: 0.2em\n  border-left: 0.2em solid var(--color-admonition-title)\n  box-shadow: 0 0.2em 0.5em rgba(0, 0, 0, 0.05), 0 0 0.0625em rgba(0, 0, 0, 0.1)\n\n  font-size: var(--admonition-font-size)\n\n  overflow: auto\n  page-break-inside: avoid\n\n  // First element should have no margin, since the title has it.\n  > :nth-child(2)\n    margin-top: 0\n\n  // Last item should have no margin, since we'll control that w/ padding\n  > :last-child\n    margin-bottom: 0\n\n\n  // Defaults for all admonitions\n  .admonition-title\n    position: relative\n    margin: 0 -0.5em 0.5em\n    padding: 0.5em 0.5em 0.5em 2em\n\n    font-weight: 500\n    font-size: var(--admonition-title-font-size)\n    background-color: var(--color-admonition-title-background)\n\n    line-height: 1.3\n\n    // Our fancy icon\n    &::before\n      content: \"\"\n      position: absolute\n      left: 0.5em\n      width: 1em\n      height: 1em\n      // color: var(--color-admonition-title)\n      background-color: var(--color-admonition-title)\n\n      mask-image: var(--icon-admonition-default)\n      mask-repeat: no-repeat\n\n  //\n  // Variants\n  //\n  @each $type, $value in $admonitions\n    &.#{$type}\n      border-left-color: var(--color-admonition-title--#{$type})\n      > .admonition-title\n        background-color: var(--color-admonition-title-background--#{$type})\n        &::before\n          background-color: var(--color-admonition-title--#{$type})\n          mask-image: var(--icon-#{nth($value, 2)})\n\n.admonition-todo > .admonition-title\n  text-transform: uppercase\n","img\n    box-sizing: border-box\n    max-width: 100%\n    height: auto\n\nimg, figure\n    &.align-left\n        clear: left\n        float: left\n        margin-right: 1em\n\n    &.align-right\n        clear: right\n        float: right\n        margin-right: 1em\n\n    &.align-center, &.align-default\n        display: block\n        margin-left: auto\n        margin-right: auto\n\nfigcaption\n    font-style: italic\n    text-align: center\n","table\n    border-radius: 0.2em\n    border-spacing: 0\n    border-collapse: collapse\n\n    box-shadow: 0 0.2em 0.5em rgba(0, 0, 0, 0.05), 0 0 0.0625em rgba(0, 0, 0, 0.1)\n\n    & > caption\n        text-align: center\n        margin-bottom: 0.25em\n\nth\n    background: var(--color-background-secondary)\n    color: var(--color-foreground-primary)\n\ntd,\nth\n    // Space things out properly\n    padding: 0 0.25em\n\n    // Get the borders looking just-right.\n    border-left: 1px solid var(--color-background-border)\n    border-right: 1px solid var(--color-background-border)\n    border-bottom: 1px solid var(--color-background-border)\n\n    p\n        margin: 0.25em\n    &:first-child\n        border-left: none\n    &:last-child\n        border-right: none\n",":root{--color-foreground-primary: black;--color-foreground-secondary: #5a5c63;--color-foreground-border: #878787;--color-background-primary: white;--color-background-secondary: #f8f9fb;--color-background-border: #eeebee;--icon-search: url('data:image/svg+xml;charset=utf-8,<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" fill=\"none\" stroke-linecap=\"round\" stroke-linejoin=\"round\"><path stroke=\"none\" d=\"M0 0h24v24H0z\"/><circle cx=\"10\" cy=\"10\" r=\"7\" /><line x1=\"21\" y1=\"21\" x2=\"15\" y2=\"15\" /></svg>');--icon-pencil: url('data:image/svg+xml;charset=utf-8,<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 24 24\"><path d=\"M20.71 7.04c.39-.39.39-1.04 0-1.41l-2.34-2.34c-.37-.39-1.02-.39-1.41 0l-1.84 1.83 3.75 3.75M3 17.25V21h3.75L17.81 9.93l-3.75-3.75L3 17.25z\"/></svg>');--icon-abstract: url('data:image/svg+xml;charset=utf-8,<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 24 24\"><path d=\"M4 5h16v2H4V5m0 4h16v2H4V9m0 4h16v2H4v-2m0 4h10v2H4v-2z\"/></svg>');--icon-info: url('data:image/svg+xml;charset=utf-8,<svg xmlns=\"htt